@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
html{box-sizing:border-box}body{margin:0;min-height:100vh;font-family:"Open Sans",sans-serif;background:#161a1d;color:#f5f3f4}.header{font-size:25px;text-align:center;margin:25px 0 15px 0;letter-spacing:5px;z-index:1}@media(min-width: 768px){.header{font-size:60px}}.loader{position:fixed;top:0;bottom:0;left:0;right:0;z-index:0}.loader img{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.image-container{margin:10px}@media(min-width: 768px){.image-container{margin:10px 15%}}@media(min-width: 1280px){.image-container{margin:10px 17.5%}}.image-container img{width:100%;margin-top:5px}
.loader{position:fixed;top:0;bottom:0;left:0;right:0;-webkit-transform:scale(0.25);transform:scale(0.25)}.loader img{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}
